.kodfun-galeri-base{
    background: #fff;
    margin: auto;
    padding: 2rem;
}

.kodfun-galeri {
    display: flex;
    height: 20rem;
    gap: 1rem;
    max-width: 900px;
    margin: 1rem auto;
}

.kodfun-galeri > div {
    flex: 1;
    border-radius: 1rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
    transition: all .8s cubic-bezier(.25, .4, .45, 1.4);
}

.kodfun-galeri > div:hover {
    flex: 5;
}